<template><div class="markdown-body"><h1>分页</h1>
<p><code v-pre="">mu-pagination</code> 组件用于分隔数据集。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-pagination :total=\&quot;1000\&quot; :current.sync=\&quot;current\&quot;&gt;&lt;/mu-pagination&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    data () {\n        return {\n            current: 1\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-pagination :total="1000" :current.sync="current"></mu-pagination>

</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-pagination</span> <span class="hljs-attr">:total</span>=<span class="hljs-string">"1000"</span> <span class="hljs-attr">:current.sync</span>=<span class="hljs-string">"current"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-pagination</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">current</span>: <span class="hljs-number">1</span>
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="pagination-props"><a class="header-anchor" href="#pagination-props" aria-hidden="true">¶</a> Pagination Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>total</td>
<td>总条目数</td>
<td>Number</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>current</td>
<td>当前页数, 支持 <code v-pre="">.sync</code> 修饰符</td>
<td>Number</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>page-size</td>
<td>每页显示条目个数</td>
<td>Number</td>
<td>—</td>
<td>10</td>
</tr>
<tr>
<td>page-count</td>
<td>页码按钮的数量，当总页数超过该值时会折叠</td>
<td>Number</td>
<td>大于等于 5 且小于等于 21 的奇数</td>
<td>7</td>
</tr>
</tbody>
</table>
<h2 id="pagination-events"><a class="header-anchor" href="#pagination-events" aria-hidden="true">¶</a> Pagination Events</h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>介绍</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>当前页更改时触发事件</td>
<td>—</td>
</tr>
</tbody>
</table>

</div class="markdown-body"></template>

<script>
export default {
    data () {
        return {
            current: 1
        }
    }
}
</script>


